import {
  request
} from "./request"
//查询用户列表
export function queryUserList(user, pageNum, pageSize) {
  return request({
    method: 'POST',
    url: '/user/queryList',
    params: {
      pageNum,
      pageSize
    },
    data: user
  })
}
//登录
export function login(username, password) {
  return request({
    url: '/user/login',
    params: {
      username,
      password
    }

  })
}
//退出登录
export function logout(username, password) {
  return request({
    url: '/user/logout'
  })
}
//获取用户数据
export function details(username, password) {
  return request({
    url: '/user/details'
  })
}